/* Animated border for core problems cards */
@keyframes border-rotate {
    100% {
      transform: translate(-50%, -50%) rotate(1turn);
    }
  }
.animated-border {
    position: relative;
    overflow: hidden;
  }
.animated-border::before {
    content: '';
    position: absolute;
    z-index: -2;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(0deg);
    width: 99999px;
    height: 99999px;
    background-repeat: no-repeat;
    background-position: 0 0;
    /* Border color using terminal lime */
    background-image: conic-gradient(
      rgba(0,0,0,0),
      hsl(var(--accent-lime)),
      rgba(0,0,0,0) 25%
    );
    /* Change speed here - 4s */
    animation: border-rotate 4s linear infinite;
    opacity: 0;
  }
.animated-border.active::before {
    opacity: 1;
  }
.animated-border::after {
    content: '';
    position: absolute;
    z-index: -1;
    /* border width - keeping 2px */
    left: 2px;
    top: 2px;
    /* double the px from the border width left */
    width: calc(100% - 4px);
    height: calc(100% - 4px);
    /* bg color */
    background: white;
    /* box border radius - adjust for border width */
    border-radius: inherit;
  }
.animated-border.bg-terminal-dark-teal-alt::after {
    background: hsl(var(--bg-dark-teal-alt));
  }

